* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: rgb(227, 205, 205);
}
.cards{
    width:100%;
    height:auto ;
}
.heading{
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 2rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-transform: uppercase;
    text-shadow: rgb(0, 0, 0);
    letter-spacing: 2px;
    font-weight: 500;
    color: burlywood;
    margin-bottom: 30px;
    background-color: rgb(175, 48, 101);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}
.card{
    margin:7%;
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    background-color: rgb(255, 255, 255);
    width:18%;
    border-radius: 12px;
    box-shadow: 0px 13px 10px -7px rgba(0,0,0,0.1);
    float:left;
}
.card:hover{
    box-shadow: 0px 30px 18px -8px rgb(0, 0, 0);
    transform: scale(1.05,1.05);
}
.card_img{
    height: 300px;
    width: 100%;
    border-top-right-radius: 12px;
    border-top-left-radius: 12px;
}
.card_info{
    background-color: white;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    padding:16px 24px 24px 24px;
    background-color: white;
}
.card_category{
    margin-top: 5px;
    margin-bottom: 5px;
    font-family: "Raleway",sans-serrif;
    text-transform:uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color:#868686;
    background-color: #fff;
}
.card_title{
    margin-top: 5px;
    margin-bottom: 10px;
    font-family: "Raleway",sans-serrif;
    background-color: #fff;
    text-transform: capitalize;
}
button{
    padding: 5px 8px;
    font-family: "Raleway",sans-serrif;
    text-transform:uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color:#868686;
    background-color: #fff;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
}
@media(max-width:768px){
    .card{
        float:none;
        margin:5%;
        width:100%;
    }
}